import React, { useState } from "react";
import pic from "./facebook48.png";
const Contact = () => {
  const [data, setData] = useState({
    name: "",
    email: "",
    number: "",
    message: "",
    status: "Send",
  });
  const { name, email, number, message, status } = data;
  const handleChange = (event) => {
    const { value, name } = event.target;
    setData({
      ...data,
      [name]: value,
    });
  };
  const handleSubmit = async (event) => {
    event.preventDefault();
    setData({ status: "sending" });
    try {
      const response = await fetch(
        "https://v1.nocodeapi.com/jabjab/google_sheets/YMiuWzIzIbfRndSA?tabId=sheet1",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify([
            [name, email, number, message, new Date().toLocaleString()],
          ]),
        }
      );
      await response.json();
      if (response) {
        alert("message sent");
      } else {
        alert("failed");
      }
      setData({
        name: "",
        email: "",
        number: "",
        message: "",
        status: "Send",
      });
    } catch (err) {
      console.error();
    }
  };
  let buttonText = status;
  return (
    <form onSubmit={handleSubmit}>
      <div className="contact">
        <div id="inp">
          <label htmlFor="name">Name:</label>
          <br />
          <input
            type="text"
            name="name"
            value={name}
            onChange={handleChange}
            required
          />
          <br />
          <label htmlFor="email">Email:</label>
          <br />
          <input
            type="email"
            name="email"
            value={email}
            onChange={handleChange}
            required
          />
          <br />
          <label htmlFor="number">Phone Number:</label>
          <br />
          <input
            type="number"
            name="number"
            value={number}
            onChange={handleChange}
            required
          />
        </div>
        <div id="other-form">
          <label htmlFor="message">Message:</label>
          <br />
          <textarea
            name="message"
            value={message}
            onChange={handleChange}
            required
          />
          <input id="submit" type="submit" value={buttonText} />
        </div>

        <h4>
          contact person: Giorgi Tsikaradze, Phone: 555 78 78 78, Email:
          gtsikaradze@gmail.com
        </h4>

        <a href="#">
          <img src={pic} alt="Facebook"></img>
        </a>
      </div>
    </form>
  );
};

export default Contact;
